@import 'variables.scss';
@import 'animations';

@font-face {
  font-family: 'Keep on Truckin';
  src: url('fonts/KeeponTruckin.ttf');
}

@keyframes img-packaging-loading {
  0% {
    transform: translateY(2rem);
    opacity: 0%;
  }
  100% {
    transform: translateY(0);
    opacity: 100%;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body,
#root {
  // Legacy fallback
  min-height: 100vh;
  min-height: 100svh;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Outfit', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  width: 100%;
  max-width: $max-width-section;
  font-size: 4rem;
  margin: 4rem 0 0 0;
  padding: 0 2rem;
  color: $clr-brown;
  text-shadow: clamp(2px, 0.25vw, 4px) clamp(2px, 0.25vw, 4px) 0px #ffefc8;
}

#missing-page-text {
  margin: 0;
  margin-bottom: 2rem;
}

h2 {
  margin: 0;
  margin-bottom: 1rem;
  width: 100%;
  font-size: 2rem;
  color: $clr-font-black;
}

p {
  font-size: 1.2rem;
  color: $clr-font-black;
}

section {
  max-width: $max-width-section;
  width: 100%;
  padding: 2em;
}

.cta-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2em 0;
  max-width: 640px;
  text-align: center;
  //border: 1px solid $clr-orange;
  border-radius: $border-radius;
  a {
    margin: 1em 0;
  }
}

img {
  display: block;
  border-radius: $border-radius;
}

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  white-space: pre-line;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 1000;
  background-color: white;
  box-shadow: 0 0 2px hsl(0, 0%, 66%);
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  max-width: 100%;
  overflow-x: hidden;
}

.button {
  padding: 12px 16px;
  width: fit-content;
  border-radius: 4px;
  text-decoration: none;
  color: white;
  background-color: $clr-brown;
  font-size: 1.2rem;
  box-shadow: 6px 6px 0 $clr-beige;
  transition: $dur-short;
  &:hover {
    background-color: hsl(27, 41%, 35%);
    box-shadow: 0px 0px 0 $clr-beige;
  }
}

nav {
  display: flex;
  margin: 0;
  padding: 0 1rem;
  width: 100%;
  height: 55px;
  max-width: 1440px;
  justify-content: space-between;
  align-items: center;
  #logo {
    height: 42px;
  }
  ul {
    display: flex;
    padding: 0;
    list-style-type: none;
  }
  a {
    padding: 0px 16px;
  }
  li a {
    text-decoration: none;
    padding: 12px 16px;
    font-size: 1.1rem;
    font-weight: 400;
    color: black;
    transition-duration: $dur-short;
    &:hover {
      color: $clr-brown;
    }
  }
}

// nav-link active
.active {
  color: $clr-brown;
}
#menu {
  display: none;
  background-color: $clr-beige;
  border-radius: $border-radius;
  outline: 1px solid transparent;
  transition-duration: $dur-short;
  &:hover {
    outline: 1px solid $clr-brown;
  }
}
#menu-button {
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
  width: 36px;
  height: 36px;
  opacity: 60%;
  transition-duration: $dur-short;
  &:hover {
    opacity: 80%;
  }
  img {
    border-radius: 0;
    width: 16px;
    height: 16px;
  }
}

#title-home {
  margin: 0.5rem 0;
  padding: 0;
  font-family: 'Keep on Truckin';
  font-size: 4.8rem;
  letter-spacing: 4px;
  text-align: left;
  font-weight: 600;
  color: $clr-brown;
  text-shadow: 6px 6px 0px #ffefc8;
}

#subtitle-home {
  margin: 8px 0;
  font-weight: 600;
  font-size: 2rem;
  color: $clr-brown;
}

#sales-pitch {
  line-height: 1.3;
  margin-top: 1em;
  margin-bottom: 2em;
}

.container-bg {
  display: grid;
  grid-template-columns: 2fr 1fr;
  padding: 4em 2em;
  gap: clamp(0em, 2vw, 2em);
  max-width: 1440px;
  div {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  div:first-child {
    padding-left: 2em;
  }
  img {
    width: 20em;
    margin: auto;
  }
}

#hero-images {
  animation: img-packaging-loading 0.5s forwards;
  position: relative;
  top: 0;
  left: 0;
  img {
    inset: 0;
    top: 0;
    left: 0;
  }
  img:first-child {
    animation: fade-out 0.5s forwards;
    animation-delay: 1.25s;
    pointer-events: none;
    z-index: 0;
  }
  img:nth-child(2) {
    animation: fade-in 0.5s forwards;
    animation-delay: 1.25s;
    position: absolute;
    opacity: 0%;
    z-index: -2;
  }
  img:nth-child(3) {
    animation: translate-down 0.5s forwards, fade-in 0.5s forwards;
    animation-delay: 1.5s;
    position: absolute;
    opacity: 0%;
    z-index: -3;
  }
  img:nth-child(4) {
    animation: translate-down 0.5s forwards, fade-in 0.5s forwards;
    animation-delay: 1.75s;
    position: absolute;
    opacity: 0%;
    z-index: -3;
  }
  img:last-child {
    animation: translate-left 0.5s forwards, fade-in 0.5s forwards;
    animation-delay: 2s;
    position: absolute;
    opacity: 0%;
    z-index: -3;
  }
}

canvas {
  max-width: 100%;
}

//Footer stuff
footer {
  padding: 1rem 1rem;
  font-size: 0.875rem;
  text-align: center;
  color: $clr-brown;
}
#footer-embellishment-container {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: $max-width-section;
  padding: 0 2rem;
  transform: translateY(16px);
  img {
    height: 64px;
    width: auto;
  }
  div:nth-child(2),
  div:nth-child(4) {
    img {
      width: 100%;
      object-fit: cover;
    }
  }
  div:last-child {
    transform: scaleX(-1);
  }
}

// Auxillary classes for styling
.diagonal-lh {
  position: relative;
  &::after {
    content: '';
    background-color: $clr-orange;
    position: absolute;
    z-index: -1;
    inset: 0;
    transform: skewY(-3deg);
  }
}
